.hv5-root {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24px 14px;
  background:
    radial-gradient(circle at 15% 8%, #ffe2d3 0%, #fff0e8 32%, transparent 52%),
    radial-gradient(circle at 87% 20%, #d6f8f0 0%, #ebfffa 34%, transparent 54%),
    radial-gradient(circle at 45% 100%, #e6efff 0%, #f2f6ff 28%, transparent 46%),
    #f4efe6;
}

.hv5-phone {
  position: relative;
  width: 300px;
  height: 620px;
  border-radius: 46px;
  background: #f2f2f3;
  border: 5px solid #cfd0d4;
  overflow: hidden;
  box-shadow: 0 24px 48px rgba(0, 0, 0, 0.18);
}

.hv5-phone::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 26px;
  background: #000;
  border-radius: 999px;
  z-index: 100;
}

.hv5-page {
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  position: relative;
}

.hv5-status {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 20px 8px;
  font-size: 12px;
  font-weight: 700;
  background: #f2f2f3;
}

.hv5-battery {
  position: relative;
  width: 18px;
  height: 10px;
  border: 1.5px solid #111;
  border-radius: 2px;
  display: inline-flex;
  align-items: center;
  padding: 1px;
  box-sizing: border-box;
}

.hv5-battery__level {
  width: 100%;
  height: 100%;
  background: #111;
  border-radius: 1px;
}

.hv5-battery__cap {
  position: absolute;
  right: -3px;
  top: 2px;
  width: 2px;
  height: 4px;
  background: #111;
  border-radius: 1px;
}

.hv5-content {
  flex: 1;
  overflow-y: auto;
  padding: 12px;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.hv5-content::-webkit-scrollbar {
  display: none;
}

.hv5-shell {
  border-radius: 30px;
  border: 1px solid #eaded0;
  background:
    radial-gradient(circle at 92% 10%, rgba(255, 126, 95, 0.15), transparent 30%),
    radial-gradient(circle at 10% 18%, rgba(0, 207, 167, 0.12), transparent 35%),
    #fff;
  box-shadow: 0 20px 44px rgba(30, 64, 175, 0.08);
  padding: 14px;
}

.hv5-pulse-row {
  display: flex;
  gap: 6px;
  overflow-x: auto;
  scrollbar-width: none;
  padding-bottom: 2px;
}

.hv5-pulse-row::-webkit-scrollbar {
  display: none;
}

.hv5-pulse-pill {
  border-radius: 999px;
  border: 1px solid #d4d4d8;
  background: #fafafa;
  color: #27272a;
  font-size: 10px;
  font-weight: 800;
  white-space: nowrap;
  padding: 6px 10px;
}

.hv5-pulse-pill--accent {
  border-color: #fdba74;
  background: #fff1df;
  color: #c2410c;
}

.hv5-banner {
  border-radius: 24px;
  border: 1px solid #fca5a5;
  background: linear-gradient(135deg, #fff0ef 0%, #fffceb 100%);
}

.hv5-highlight {
  border-radius: 18px;
  border: 1px dashed #14b8a6;
  background: #f0fdfa;
}

.hv5-nav {
  flex-shrink: 0;
  margin: 0 8px 8px;
  border: 1px solid #e5e7eb;
  background: rgba(255, 255, 255, 0.96);
  backdrop-filter: blur(12px);
  border-radius: 18px;
  padding: 8px 6px 10px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 4px;
}

.hv5-nav__item {
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2px;
  color: #3f3f46;
  border-radius: 12px;
  padding: 4px 2px;
}

.hv5-nav__item--active {
  background: #111827;
  color: #fff;
}

.hv5-nav__icon {
  font-size: 14px;
  line-height: 1;
}

.hv5-nav__label {
  font-size: 10px;
  line-height: 1;
  font-weight: 700;
}
